@mixin mobile{
    @media screen and (max-width:480px){
        @content;
    }
}
@mixin tablet{
    @media screen and (max-width:768px){
        @content;
    }
}
@mixin laptop{
    @media screen and (max-width:1200px){
        @content;
    }
}

.search{
    border-bottom: 1px solid gray;

    .searchForm{
        padding: 10px;
        input{
            background-color: transparent;
            border: none;
            color: white;
            outline: none;
            &::placeholder{
                color: lightgray;
            }
        }
    } 

    .userChat{
        padding: 10px;
        display: flex;
        align-items: center;
        gap: 10px;
        color: white;
        cursor: pointer;

        &:hover{
            background-color: #2f2d52;
        }
        img{
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
        }
    }
    .userChatInfo{
        span{
            font-size: 18px;
            font-weight: 500;
        }
        p{
            font-size: 14px;
            color: lightgray;
        }
    }
}